import React, { useContext, useState } from 'react';
import ApiContext from '../context/ApiContext';
import EditorContainer from './editor/EditorContainer';
import VideoEditor from './editor/videos/VideoEditor';
import SeriesSchema from '../model/SeriesSchema';
import EpisodeSchema from '../model/EpisodeSchema';

export default function SeriesEditor() {
  const api = useContext(ApiContext);

  const [mode, setMode] = useState(EditorContainer.IDLE);
  const [uuid, setUuid] = useState('');

  const [subMode, setSubMode] = useState(EditorContainer.IDLE);
  const [subUuid, setSubUuid] = useState('');

  const setUuidAndClearSubUuid = (state) => {
    setUuid(state);
    setSubUuid('');
    setSubMode(EditorContainer.IDLE);
  };

  const epQueries = {
    fetchAll: async function fetchEpisodes() { return api.series.episodes.fetchAll(uuid); },
    create: async function createEpisode(data) { return api.series.episodes.create(uuid, data); },
    fetch: api.media.fetch,
    update: api.media.update,
  };

  // console.log(epQueries);

  return (
    <>
      <EditorContainer
        name="series"
        schema={SeriesSchema}
        queries={api.series}
        taggable
        modeState={[mode, setMode]}
        uuidState={[uuid, setUuidAndClearSubUuid]}
      />
      {mode === EditorContainer.UPDATE && uuid && (
        <>
          <EditorContainer
            name={`episodes${uuid}`}
            schema={EpisodeSchema}
            queries={epQueries}
            modeState={[subMode, setSubMode]}
            uuidState={[subUuid, setSubUuid]}
          />
          {subMode === EditorContainer.UPDATE && subUuid && (
            <VideoEditor uuid={subUuid} />
          )}
        </>
      )}
    </>
  );
}
