import React from 'react';
import PropTypes from 'prop-types';

import VideoSchema from '../../../model/VideoSchema';

export default function VideoEditRow(props) {
  const {
    data,
    onChange,
    onSave,
    onCancel,
  } = props;

  const handleChange = (prop, e) => {
    onChange({
      ...data,
      [prop]: e.target.value,
    });
  };

  return (
    <tr>
      <td>
        <select
          onChange={(e) => { handleChange('quality', e); }}
          value={data.quality}
        >
          {VideoSchema.properties.quality.enum.map((option) => (
            <option key={option} value={option}>{option}</option>
          ))}
        </select>
      </td>
      <td>
        <select
          onChange={(e) => { handleChange('videoType', e); }}
          value={data.videoType}
        >
          {VideoSchema.properties.videoType.enum.map((option) => (
            <option key={option} value={option}>{option}</option>
          ))}
        </select>
      </td>
      <td>
        <input
          type="text"
          value={data.url}
          onChange={(e) => { handleChange('url', e); }}
        />
      </td>
      <td>
        <button type="button" onClick={onSave}>save</button>
        { onCancel && <button type="button" onClick={onCancel}>cancel</button> }
      </td>
    </tr>
  );
}

VideoEditRow.propTypes = {
  data: PropTypes.shape({
    url: PropTypes.string,
    videoType: PropTypes.string,
    quality: PropTypes.string,
  }).isRequired,
  onChange: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  onCancel: PropTypes.func,
};

VideoEditRow.defaultProps = {
  onCancel: undefined,
};
