export default function ContentTagApi(axios, route) {
  const fetchAll = async function fetchAll(uuid) {
    const res = await axios.get(`${route}/${uuid}/tags`);
    return res.data._embedded?.tags.map(({ name }) => name);
  };

  const add = async function add(uuid, tag) {
    return axios.post(`${route}/${uuid}/tags`, { name: tag });
  };

  const remove = async function remove(uuid, tag) {
    return axios.delete(`${route}/${uuid}/tags/${tag}`);
  };

  return {
    fetchAll,
    add,
    remove,
  };
}
