import { Validator } from 'jsonschema';

const v = new Validator();

export function validate(instance, schema) {
  return v.validate(instance, schema, { throwAll: true });
}

export function validatePartial(instance, schema) {
  Object.keys(schema.properties).forEach((key) => {
    v.validate(instance[key], schema.properties[key], { throwAll: true });
  });

  return true;
}

/* eslint default-param-last:0 */
export function sliceObjectToSchema(instance = {}, schema) {
  const newObj = {};
  Object.keys(schema.properties).forEach((key) => {
    newObj[key] = instance[key];
  });
  return newObj;
}
