import { useQuery, useMutation, useQueryClient } from 'react-query';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import styled from 'styled-components';

import { sliceObjectToSchema } from '../../util/SchemaTools';
import DetailForm from './DetailForm';
import ListSelector from './ListSelector';
import TagEditor from './tags/TagEditor';

const Wrapper = styled.div`
  display: flex;
`;

const LeftBox = styled.div`
  display: block;
  width: 200px;
  margin-right: 20px;

  & > * {
    display: block;
    width: 100%;
  }
`;

const RightBox = styled.div``;

const SectionWrapper = styled.section`
  box-sizing: border-box;
  margin: 1em 0;
  border: thin solid #ccc;
  padding: 5px;
`;

const ThumbnailWrapper = styled(SectionWrapper)`
  figure {
    display: inline-block;
    width: 300px;
  }

  form {
    display: inline-block;
  }
`;

const UPDATE = 0;
const CREATE = 1;
const IDLE = 2;

// ***
// TODO: lift mode / id into parent state
//   to sync EpisodeContainers for series and VideoContainers for non-series.
// ***

export default function EditorContainer(props) {
  const {
    name,
    schema,
    queries, // object containing query functions
    taggable,
    modeState,
    uuidState,
  } = props;

  const [mode, setMode] = modeState;
  const [uuid, setUuid] = uuidState;
  const [formData, setFormData] = useState({});
  const [dataNeedsUpdate, setDataNeedsUpdate] = useState(false);

  const getFormData = () => uuid || mode === CREATE ? formData : {};

  const qc = useQueryClient();

  const collectionQuery = useQuery(name, queries.fetchAll);
  const query = useQuery([name, uuid], () => queries.fetch(uuid), {
    enabled: !!uuid,
    onSuccess: (responseData) => {
      if (dataNeedsUpdate) {
        setFormData(sliceObjectToSchema(responseData, schema));
        setDataNeedsUpdate(false);
      }
    },
  });

  const updateQuery = useMutation((data) => queries.update(uuid, data));
  const createQuery = useMutation((data) => queries.create(data));

  const clearThumbnailUpload = () => { document.querySelector('#upload').value = null; };

  const save = () => {
    if (mode === UPDATE) {
      updateQuery.mutate(formData, {
        onSuccess: () => {
          qc.invalidateQueries(name, { exact: true });
          qc.invalidateQueries([name, uuid]);
          clearThumbnailUpload();
        },
      });
    } else if (mode === CREATE) {
      createQuery.mutate(formData, {
        onSuccess: () => {
          qc.invalidateQueries(name, { exact: true });
          setMode(IDLE); // TODO: get new ID from API response - set mode UPDATE, set ID accordingly
          setUuid('');
          clearThumbnailUpload();
        },
      });
    }

    // TODO: use a better file uploader
  };

  const changeId = (nextId) => {
    setUuid(nextId);

    if (!nextId) {
      setFormData({});
      setMode(IDLE);
    } else {
      setDataNeedsUpdate(true);
      setMode(UPDATE);
    }
  };

  const createNew = () => {
    setUuid('');
    setFormData({});
    setMode(CREATE);
  };

  const listItems = collectionQuery.data?.map((item) => ({
    value: item.uuid,
    label: item.episodeNumber
      ? `S${item.seasonNumber ?? '0'}:${item.episodeNumber}. ${item.title}`
      : item.title,
  }));

  const editFormData = (data) => {
    setFormData({
      ...formData,
      ...data,
    });
  };

  const handleThumbnailUpload = (file) => {
    editFormData({
      thumbnail: file,
    });
  };

  const thumbnailURL = query.data?.thumbnail
    ? query.data.thumbnail[query.data.thumbnail.length - 1].url
    : undefined;

  return (
    <Wrapper>
      <LeftBox>
        <ListSelector
          list={listItems}
          value={uuid}
          callback={changeId}
        />
        <button type="button" onClick={createNew}>Create new</button>
      </LeftBox>

      <RightBox>
        <DetailForm
          heading={(mode === UPDATE && `Editing ${query.data?.title}...`) || (mode === CREATE && 'Create new...') || undefined}
          schema={schema}
          data={getFormData()}
          onChange={editFormData}
          onUpload={handleThumbnailUpload}
          onSave={save}
          disabled={mode === IDLE}
        />

        {mode !== IDLE && (
          <ThumbnailWrapper>
            <h3>Thumbnail</h3>
            <figure>
              { thumbnailURL ? (
                <>
                  <figcaption>Current thumbnail:</figcaption>
                  <img src={thumbnailURL} alt="thumb" />
                </>
              ) : (
                <figcaption>No thumbnail uploaded</figcaption>
              )}
            </figure>
            <form>
              <input
                id="upload"
                type="file"
                accept=".jpg"
                name="thumbnail"
                onChange={(e) => handleThumbnailUpload(e.target.files[0])}
              />
              { document.querySelector('#upload')?.files.length > 0 && <button type="button" onClick={clearThumbnailUpload}>Clear</button> }
            </form>
          </ThumbnailWrapper>
        )}

        {taggable && mode === UPDATE && (
          <SectionWrapper>
            <TagEditor
              uuid={uuid}
              queries={queries.tags}
            />
          </SectionWrapper>
        )}
      </RightBox>
    </Wrapper>
  );
}

Object.assign(EditorContainer, {
  UPDATE,
  CREATE,
  IDLE,
});

EditorContainer.propTypes = {
  /* eslint react/forbid-prop-types:0 */
  name: PropTypes.string.isRequired,
  schema: PropTypes.object.isRequired,
  queries: PropTypes.shape({
    fetchAll: PropTypes.func.isRequired,
    fetch: PropTypes.func.isRequired,
    update: PropTypes.func.isRequired,
    create: PropTypes.func.isRequired,
    tags: PropTypes.object,
  }).isRequired,
  taggable: PropTypes.bool,
  modeState: PropTypes.array.isRequired,
  uuidState: PropTypes.array.isRequired,
};

EditorContainer.defaultProps = {
  taggable: false,
};
