import StreamableSchema from '../model/StreamableSchema';
import VideoSchema from '../model/VideoSchema';
import { validate, validatePartial } from '../util/SchemaTools';
import ContentTagApi from './ContentTagApi';

export default function MediaApi(axios) {
  const fetchAll = async function fetchAll() {
    const res = await axios.get('/media');
    return res.data._embedded?.media;
  };

  const create = async function create(content) {
    validate(content, StreamableSchema);
    return axios.post('/media', content);
  };

  const fetch = async function fetch(uuid) {
    const res = await axios.get(`/media/${uuid}`);
    return res.data;
  };

  const update = async function update(uuid, params) {
    validatePartial(params, StreamableSchema);
    return axios.put(`/media/${uuid}`, params);
  };

  const remove = async function remove(uuid) {
    return axios.delete(`/media/${uuid}`);
  };

  const fetchAllVideos = async function fetchAllVideos(uuid) {
    const res = await axios.get(`/media/${uuid}/videos`);
    return res.data._embedded?.videos;
  };

  const createVideo = async function createVideo(uuid, params) {
    validate(params, VideoSchema);
    return axios.post(`/media/${uuid}/videos`, params);
  };

  const updateVideo = async function updateVideo(uuid, id, params) {
    validatePartial(params, VideoSchema);
    return axios.put(`/media/${uuid}/videos/${id}`, params);
  };

  const removeVideo = async function removeVideo(uuid, id) {
    return axios.delete(`/media/${uuid}/videos/${id}`);
  };

  return {
    fetchAll,
    create,
    fetch,
    update,
    remove,
    tags: ContentTagApi(axios, '/media'),
    videos: {
      fetchAll: fetchAllVideos,
      create: createVideo,
      update: updateVideo,
      remove: removeVideo,
    },
  };
}
