const VideoSchema = {
  properties: {
    url: {
      type: 'string',
      allowEmpty: false,
      maxLength: 100,
      format: 'url',
    },

    quality: {
      type: 'string',
      enum: ['SD', 'HD', 'FHD', 'UHD'],
    },

    videoType: {
      type: 'string',
      enum: ['HLS', 'SMOOTH', 'DASH', 'MP4', 'MOV', 'M4V'],
    },
  },

  required: [
    'url',
    'quality',
    'videoType',
  ],
};

export default VideoSchema;
