import { QueryClient, QueryClientProvider } from 'react-query';
import { ReactQueryDevtools } from 'react-query/devtools';
import React, { useState } from 'react';
import styled from 'styled-components';

import Api from '../api/Api';
import ApiContext from '../context/ApiContext';
import LoginForm from './LoginForm';
import SeriesEditor from './SeriesEditor';
import MediaEditor from './MediaEditor';

const Wrapper = styled.main`
  width: 1000px;
  margin: 0 auto;
  padding-top: 3em;
`;

const Header = styled.header`
  position: fixed;
  line-height: 2em;
  height: 2em;
  background: linear-gradient(to bottom, #eee, #ccc);
  width: 100%;

  .inner {
    width: 1000px;
    margin: 0 auto;
  }

  .right {
    float:right;
  }
`;

const api = Api(process.env.REACT_APP_API_ROOT);
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnReconnect: false,
      refetchOnWindowFocus: false,
    },
  },
});

const ROOT = 0;
const SERIES = 1;
const MEDIA = 2;

export default function App() {
  // const [api] = useState(Api(process.env.REACT_APP_API_ROOT));
  const [loggedIn, setLoggedIn] = useState(false);
  const [route, setRoute] = useState(ROOT);

  const login = async (u, p) => {
    await api.login(u, p);
    setLoggedIn(api.isLoggedIn());
  };

  const logout = () => {
    api.logout();
    setLoggedIn(false);
  };

  const loadCurrentRoute = () => {
    switch (route) {
      case SERIES:
        return <SeriesEditor />;
      case MEDIA:
        return <MediaEditor />;
      default:
        return (
          <>
            <button
              type="button"
              onClick={() => { setRoute(SERIES); }}
            >
              View All Series
            </button>
            <button
              type="button"
              onClick={() => { setRoute(MEDIA); }}
            >
              View All Media
            </button>
          </>
        );
    }
  };

  /* eslint jsx-a11y/anchor-is-valid:0 */
  return (
    <QueryClientProvider client={queryClient}>
      <ApiContext.Provider value={api}>
        <Header>
          <div className="inner">
            <a href="#" onClick={() => { setRoute(ROOT); }}>home</a>
            <div className="right">
              {loggedIn && (/* eslint react/jsx-one-expression-per-line:0 */
                <>
                  hello {api.username}
                  (<a href="#" onClick={logout}>logout</a>)
                </>
              )}
            </div>
          </div>
        </Header>
        <Wrapper>
          { !loggedIn
            ? <LoginForm login={login} />
            : loadCurrentRoute()}
        </Wrapper>
        <ReactQueryDevtools />
      </ApiContext.Provider>
    </QueryClientProvider>
  );
}
